              justify-items: space-between;
              margin-top: 30px;
              height: 16px;
            "
          >
            <el-skeleton-item variant="text" style="margin-right: 16px" />
            <el-skeleton-item variant="text" style="width: 30%" />
          </div>
          <el-skeleton-item variant="text" style="margin: 30px 0 30px" />
