<script>
import { initDate } from "../utils/server.js";
import { getArticle, updateViewCount } from "../api/article.js";
import { mavonEditor } from "mavon-editor";
export default {
  data() {
    //选项 / 数据
    return {
      aid: "", //文章ID
      detailObj: {}, //返回详情数据
      haslogin: false, //是否已经登录
      userId: "", //用户id
      loading: true,
      menuList: [],
    };
  },

  methods: {
    //事件处理器
    showInitDate: function (date, full) {
      //年月日的编辑
      return initDate(date, full);
    },
    getArticleDetail: function () {
      getArticle(this.aid).then((response) => {
        this.detailObj = response;
        const markdownIt = mavonEditor.getMarkdownIt();
        // markdownIt.re
        this.detailObj.content = markdownIt.render(response.content);
      });
    },
    routeChange: function () {
      var that = this;
      that.aid =
        that.$route.query.aid == undefined
          ? 1
          : parseInt(that.$route.query.aid); //获取传参的aid
      //判断用户是否存在
      if (localStorage.getItem("userInfo")) {
        that.haslogin = true;
        that.userInfo = JSON.parse(localStorage.getItem("userInfo"));
        that.userId = that.userInfo.userId;
      } else {
        that.haslogin = false;
      }
      //获取详情接口
      this.getArticleDetail();
      updateViewCount(that.aid);
      this.timeout = setTimeout(() => {
        this.loading = false;
      }, 500);
    },
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    $route: "routeChange",
  },
  components: {
    //定义组件
  },
  created() {
    //生命周期函数
    var that = this;

    this.routeChange();
  },
};
</script>

